.file-input{
    margin-bottom: 2em;
}
.select{
    margin-bottom: 2em;
    display: flex;
    justify-content: space-evenly;
    &__title{
        margin-right: 1em;
    }
    &__status-filter-container {
        display: flex;
        height: 40px;
    }
    &__function-filter {
        display: flex;
        flex-direction: column;
    }
}
.stats{
    margin-bottom: 1em;
    &__title{
        padding: 10px;
        background-color: #3032C1;
        color:white;
    }
    &__results{
        display:flex;
        flex-wrap: nowrap;
        background-color: white;
        border-bottom: 1px solid lightgrey;
        border-right: 1px solid lightgrey;
        border-left: 1px solid lightgrey;
    }
    &__results-div{
        padding: 10px;
        width: 33%;
        &--border{
            border-right: 1px solid lightgrey;
        }
    }
}

.table-title{
    text-align: center;
}

.table-margin{
    margin: 10px;
}

.table-result{
    display: flex;
    margin-bottom: 1em;
    flex-wrap: wrap;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    &__header {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        background-color: #3032C1;
        color: white;
        padding: 5px 0px 0px 15px;
        h2 {
            margin-top: 5px;
            margin-bottom: 5px;
        }
    }
    &__collapse-button {
        margin: 0 20px 0 0;
        color: white;
        font-weight: bold;
        font-size: 40px;
        cursor: pointer;
    }
    &__copy-clipboard-button {
        color: white;
        background-color: #3032C1;
        border: none;
        border-radius: 10px;
        padding: 5px;
        cursor: pointer;
    }
    &__parse-button-container {
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 15px;
    }
    &__parse-button {
        color: white;
        background-color: green;
        border: none;
        width: 150px;
        height: 50px;
        border-radius: 10px;
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 1.4px;
        cursor: pointer;
    }
    &__row{
        flex: 100%;
        background-color: #F5F5F5;
        padding: 10px;
        &--red{
            color:red;
        }
        &--green{
            color:green;
        }
        &--overflow{
            overflow-wrap: break-word;
        }
    }
    &__column{
        width: 49%;
        overflow-wrap: break-word;
        &--border-left{
            border-left: 1px solid lightgrey;
        }
        &--time-span{
            margin-bottom: 1em;
        }
    }
    &__column-container{
        width:100%;
        display: flex;
        flex-wrap: nowrap;
    }
}
